<script>
	import companyDetailBackground from '../../pages/companyBackground/companyDetailBackground.vue'
	import investProjectManageHome from '../../pages/investmentManage/investProjectManageHome.vue'

	import {getUserlistApi,getcomPany,surveyCredit} from '../../config/api'
	import {getSubscriptionList,getQuotedPlace,directSaveFundInfo,directGetFundDetail,getProjectStatusList,directGetProjectStatus} from '../../config/apiSj'
	import { dialogConfirm,dialogConfirmHasbtn,isValidTel,isValidIdentify} from '../../config/mUtils';
	import url from '../../config/url'
	export default {
		data() {
			return {
				subprojectName:'',//当前项目的名称
				subprojectId:'',//当前项目的ID；

				// subscribeFundList: [],//认购子基金列表
				plateList:[],//上市/挂牌板块列表
				creatorList:[],//项目经理列表
				projectStatusList:[],//项目状态列表

				moneyTypeList:[
					{
						id:1,
						name:'人民币'		
					},
					{
						id:2,
						name:'美元'		
					},
				],//币种列表
				// subscribeFundType:[
				// 	{
				// 		id:1,
				// 		name:'母基金'		
				// 	},
				// 	{
				// 		id:2,
				// 		name:'自管基金'		
				// 	},
				// ],//认购基金类别
				
				
				personFundList:[],//自管基金列表
				motherList:[],//母基金列表

				commitData: {
					// subscription_info:[],
					quote_info:[],
					project_contacts:[],
				},
				copyData:null,
				isEditor:false,
				saveEditorData:{},

				type:6,//舆情固定参数6,
				showStatusBtn:false,
			}
		},
		created() {
			this.subprojectId = this.$route.query.id;
			this.getModuleInfo();
			this.getAllUserList();
			// this.getSelectList(1);
			this.toGetSubFundDetail();
			this.toGetProjectStatusList();
		},
		mounted() {},
		components: {
			companyDetailBackground,
			investProjectManageHome,
		},
		methods: {
			//修改舆情状态
			changeHideStatus(e){
				if(this.isEditor){
					if (e == 1) {
						this.commitData.is_monitor = 1;
					} else{
						this.commitData.is_monitor = 0;
					}
				}
			},

			toEnterNextStatus(){
				if(this.isEditor){
					// let message = '';
					// this.projectStatusList.forEach(element => {
					// 	if(element.id==this.commitData.project_status+1){
					// 		if(element.id==4){
					// 			message = '确定投决该项目?';
					// 		}else if(element.id>4){
					// 			message = '确定'+element.status+'该项目?';
					// 		}else{
					// 			message = '确定将该项目进行'+element.status+'?';
					// 		}
							
					// 	}
					// });
					directGetProjectStatus({
						direct_invest_id:this.subprojectId,
						status:this.commitData.project_status,
					}).then(res => {
						// console.log(res);
						if (res.data.msgNo == 1) {
							let data = res.data.payload;
							if(data.is_next){
								dialogConfirmHasbtn(data.msg).then(() => {
									this.commitData.project_status = data.status;
									this.showStatusBtn = false;
								}).catch(() => {});
							}else{
								this.showStatusBtn = false;
							}
						}
					})
					// if(message){
					// 	dialogConfirmHasbtn(message).then(() => {
					// 		this.commitData.project_status = this.commitData.project_status + 1;
					// 	}).catch(() => {});
					// }
				}
			},

			//添加项目企业挂牌信息/认购基金/联系人
			addData(type){
				if(type === 1){
                    this.commitData.subscription_info.push({
						subscription_fund_type:'',
						subscription_fund_ids:[],
						subscribeFundList:[],
					});
                }else if(type === 2){
                    this.commitData.quote_info.push({
						share_code:'',
						quoted_date:'',
						list_plate_id:'',
					});
                }else if(type === 3){
                    this.commitData.project_contacts.push({
						name:'',
						tel:'',
						identify_id:'',
						email:'',
						other_info:'',
					});
                }
				
			},

			//删除项目企业挂牌信息/认购基金/联系人
			deleteData(index,type){
				if(type === 1){
                    this.commitData.subscription_info.splice(index,1);
                }else if(type === 2){
                    this.commitData.quote_info.splice(index,1);
                }else if(type === 3){
                    this.commitData.project_contacts.splice(index,1);
                }
			},
			//编辑状态
			changeEditStatus(){
				this.isEditor = true;
				this.saveEditorData = JSON.parse(JSON.stringify(this.commitData));
			},
			//取消编辑
			toCancle(){
				this.isEditor = false;
				this.toGetSubFundDetail();
				// this.commitData = JSON.parse(JSON.stringify(this.saveEditorData));
			},
			//去保存
			toSave(){
				if(this.commitData.project_name&&this.commitData.project_com_name){
					//处理数据
					let commitData = JSON.parse(JSON.stringify(this.commitData));
					//验证格式
					let canContinue = true;
					commitData.project_contacts.forEach((element,index) => {
						if(canContinue){
							if(element.tel&&canContinue){
								if(!isValidTel(element.tel)){
									dialogConfirm('info', '手机号码格式错误!');
									canContinue = false;
								}
							}
							if(element.identify_id&&canContinue){
								if(!isValidIdentify(element.identify_id)){
									dialogConfirm('info', '身份证号格式错误!');
									canContinue = false;
								}
							}
						}
						
					});
					if(!canContinue){
						return false;
					}
					// commitData.project_contacts.forEach((element,index) => {
					// 	delete element.id;
					// });
					delete commitData.credit_code;
					
					commitData.quote_info = JSON.stringify(commitData.quote_info);
					commitData.quote_infos = commitData.quote_info;
					delete commitData.quote_info;
					commitData.project_contacts = JSON.stringify(commitData.project_contacts);
					commitData.contacts = commitData.project_contacts;
					delete commitData.project_contacts;

					//请求接口
					directSaveFundInfo(commitData).then(res => {
						if (res.data.msgNo == 1) {
							// let data  = res.data.payload;
							dialogConfirm('success', '保存成功');
							this.isEditor = false;
							this.toGetSubFundDetail();
						}else{
							dialogConfirm('info', res.data.message);
						}
					})
				}else{
					if (!this.commitData.project_name) {
						dialogConfirm("info", '项目名称不能为空');
					}else if(!this.commitData.project_com_name){
						dialogConfirm("info", '项目企业名称不能为空');
					}
				}
			},

			
			//获取上市/挂牌模板信息
			getModuleInfo(){
				//请求接口
				getQuotedPlace({
					keyword:'',
				}).then(res => {
					// console.log(res);
					if (res.data.msgNo == 1) {
						let data = res.data.payload ;
						this.plateList = data;
					}
				})
			},
			// 获取数据创建人列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.creatorList = res.data.payload.data;
					}
				})
			},

			// 获取项目状态列表
			toGetProjectStatusList(){
				getProjectStatusList().then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res);
						this.projectStatusList = res.data.payload;
					}
				})
			},
