			//获取当前子基金项目信息
			toGetSubFundDetail(){
				directGetFundDetail({
					id:this.subprojectId,
				}).then(res => {
					// console.log(res.data);
					if(res.data.msgNo == 1) {
						let data = res.data.payload;
						// data.subscription_info.forEach((element,index) => {
						// 	if(element.subscription_fund_type==1){
						// 		element.subscribeFundList = this.motherList;
						// 	}else if(element.subscription_fund_type==2){
						// 		element.subscribeFundList = this.personFundList;
						// 	}
						// });
						this.copyData = res.data.payload;
						this.commitData = res.data.payload;
						if(!this.commitData.project_charge_id){
							this.commitData.project_charge_id = '';
						}
						if(!this.commitData.project_manager_id){
							this.commitData.project_manager_id = '';
						}
						this.subprojectName = res.data.payload.project_name;
						directGetProjectStatus({
							direct_invest_id:this.subprojectId,
							status:this.commitData.project_status,
						}).then(res => {
							// console.log(res);
							if (res.data.msgNo == 1) {
								let data = res.data.payload;
								if(data.is_next){
									this.showStatusBtn = true;
								}else{
									this.showStatusBtn = false;
								}
							}
						})
					}
				})
			},

			//导出企业信息
			downLoadCompanyInfo(){
				var key = window.localStorage.getItem("htcx_server_key");
				let href = url.sourceDataUrl + "/web/Company/CompanyInfoExcel?company_id=" + this.subprojectId + '&key=' + key + '&new_type=' + this.type;
				// console.log(href)
				window.location.href = href;
			},

			// 去征信查询
			toZxQuery() {
				// console.log(this.commitData);
				// return false;
				let ae = [];
				let params = {
					companyId: this.subprojectId,
					companyName:this.commitData.project_com_name,
				};
				this.commitData.project_contacts.forEach(item => {
					if(item.status == -1) {
						let newObj = {
							idCard: item.identify_id,
							name: item.name,
							phone: '',
							type: 0,
						};
						ae.push(newObj);
					}
				});
				if(ae.length == 0) {
					dialogConfirm('info', "请先添加新的联系人");
					return false;
				}
				params.ae = ae;
				// console.log(params);
				surveyCredit(params).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '查询成功');
						this.toGetSubFundDetail();
					} else if(res.data.msgNo == 9528) {
						// this.addaccountDialogVisible2 = true;
						// this.confirmData = res.data.payload[0]
					} else {
						// dialogConfirm('info', res.data.message);
					}
				})
			},
			// 去下载征信文件
			toDownloadZx(id) {
				// console.log(id);
				// return false;
				if(id != undefined) {
					this.$router.push({
						nama: 'creditQueryView',
						query: {
							id: id,
							companyId: this.subprojectId,
							cName: this.commitData.project_com_name
						}
					})
				} else {
					let selectedPeople = [];
					// console.log(this.commitData);
					let projectContacts = JSON.parse(JSON.stringify(this.commitData.project_contacts));
					projectContacts.forEach(element => {
						// console.log(element);
						if(element.credit_id != null && element.status == 2){
							selectedPeople.push(element.credit_id);
						}
					});
					// console.log(selectedPeople);
					// return false;
					if(selectedPeople.length == 1) {
						this.$router.push({
							name: 'creditQueryView',
							query: {
								id: selectedPeople[0],
								companyId: this.subprojectId,
								cName: this.commitData.project_com_name,
							}
						})
					} else if(selectedPeople.length > 1) {
						this.$router.push({
							name: 'creditQueryView',
							query: {
								id: selectedPeople.join(','),
								companyId: this.subprojectId,
								cName: this.commitData.project_com_name,
							}
						})
					} else {
						// console.log(111111)
						dialogConfirm('info', '没有可下载的征信报告');
					}
				}
			},

			toFundRunPage(){
				let unit = '';
				if(this.copyData.unit==1){
					unit = '人民币';
				}else{
					unit = '美元';
				}
				this.$router.push({
					name: "directInvestProjectRun",
					query: {
						id: this.subprojectId,
						name: encodeURIComponent(this.copyData.project_name) ,
						unit:encodeURIComponent(unit),
					}
				})
			}
		},
	}
</script>

